import GitHubIcon from '@material-ui/icons/GitHub'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import { about } from '../../portfolio'
import './About.css'

const About = () => {
  const { name, role, description,descriptionDeux, resume, social } = about

  return (
    <div className='about center'>
      {name && (
        <h1 style={{textAlign: 'center'}}>
          Bonjour, je suis <span className='about__name'>{name}.</span>
        </h1>
      )}

      {role && <h2 className='about__role'>Un {role}.</h2>}
      <p className='about__desc'>{description && description}</p>
      <p className='about__desc'>{descriptionDeux && descriptionDeux}</p>

      <div className='about__contact center'>
        {resume && (
          <a href={resume}>
            <span type='button' className='btn btn--outline'>
              CV
            </span>
          </a>
        )}
      </div>

        <div className='about__contact center'>
            {social && (
                <>
                    {social.github && (
                        <a
                            href={social.github}
                            aria-label='github'
                            className='link link--icon'
                        >
                            <GitHubIcon />
                        </a>
                    )}

                    {social.linkedin && (
                        <a
                            href={social.linkedin}
                            aria-label='linkedin'
                            className='link link--icon'
                        >
                            <LinkedInIcon />
                        </a>
                    )}
                </>
            )}
      </div>
    </div>
  )
}

export default About
